.slick-slider {
  font-size: 0;

  .slick-slide {
    img {
      width: 100%;
      height: auto;
    }
  }

  .slick-dots {
    bottom: 0;
  }

  .slick-arrow {
    &.slick-prev,
    &.slick-next {
      &:focus,:active,:hover{
        color: $white;
        background: $green-primary;
        border: medium none;
        outline: none;
        opacity: 1;
      }

      &.slick-disabled{
        opacity: 0;
        visibility: hidden;
      }
      position: absolute;
      z-index: 50;
      top: 50%;
      width: 50px;
      height: 50px;
      margin-bottom: -25px;
      text-align: center;
      background: $green-primary;
      color: $white;
      border: medium none;
      border-radius: 50%;
      text-indent: -9999px;
      transition: visibility .3s, opacity .3s;
    }
    &.slick-next{
      right: $global-padding;
      &:after,&:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 38%;
        width: 16px;
        height: 2px;
        margin-top: 5px;
        background: $white;
        transform: rotate(-45deg);
      }
      &:before{
        transform: rotate(45deg);
        margin-top: -5px;
      }
    }
    &.slick-prev{
      left: $global-padding;
      &:after,&:before{
        content: '';
        position: absolute;
        top: 50%;
        right: 38%;
        width: 16px;
        height: 2px;
        margin-top: 5px;
        background: $white;
        transform: rotate(45deg);
      }
      &:before{
        transform: rotate(-45deg);
        margin-top: -5px;
      }
    }
  }
}



